let snake = null;
let food = null;
let foodB = null;
let award = null;
let isLock = false;

import bgWav from '@/assets/wav/boot.wav'
import eatFoodWav from '@/assets/wav/apple.wav'
import eatBonusWav from '@/assets/wav/bonus.wav'
import hitWallWav from '@/assets/wav/hitWall.wav'
