import appleImg from '@/assets/images/apple.png'
import bodyLeftImg from '@/assets/images/body_left.png'
import bodyBottomImg from '@/assets/images/body_bottom.png'
import bodyRightImg from '@/assets/images/body_right.png'
import bodyTopImg from '@/assets/images/body_top.png'
import footerImg from '@/assets/images/footer.png'
import headerLeftImg from '@/assets/images/header_left.png'
import headerBottomImg from '@/assets/images/header_bottom.png'
import headerRightImg from '@/assets/images/header_right.png'
import headerTopImg from '@/assets/images/header_top.png'
import bFoodImg from '@/assets/images/b.png'
import dieLeftImg from '@/assets/images/die_left.png'
import dieRightImg from '@/assets/images/die_right.png'
import dieTopImg from '@/assets/images/die_top.png'
import dieBottomImg from '@/assets/images/die_bottom.png'
