// 所有音频
const AUDIO = {
    BG: null,
    EATFOOD: null,
    EATBONUS: null,
    DIE: null,
    GAMEOVER: null,
    // CONGRATS: null,
    UPGRADE: null,
}

// 所有图片
const IMGS = {
    APPLE: null,
    BODYLEFT: null,
    BODYRIGHT: null,
    BODYTOP: null,
    BODYBOTTOM: null,
    FOOTER: null,
    HEADERLEFT: null,
    HEADERRIGHT: null,
    HEADERTOP: null,
    HEAERBOTTOM: null,
    BFOOD: null,
    DIELEFT: null,
    DIERIGHT: null,
    DIETOP: null,
    DIEBOTTOM: null,
    AWARD: null
}

import digitImg0 from "@/assets/images/digit_0.png";
import digitImg1 from "@/assets/images/digit_1.png";
import digitImg2 from "@/assets/images/digit_2.png";
import digitImg3 from "@/assets/images/digit_3.png";
import digitImg4 from "@/assets/images/digit_4.png";
import digitImg5 from "@/assets/images/digit_5.png";
import digitImg6 from "@/assets/images/digit_6.png";
import digitImg7 from "@/assets/images/digit_7.png";
import digitImg8 from "@/assets/images/digit_8.png";
import digitImg9 from "@/assets/images/digit_9.png";
import levelImg from "@/assets/images/level.png";


// 监听图片加载完成
function loadImage(src) {
    return new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = function () {
            resolve({ err: null, data: img });
        };
        img.onerror = function () {
            reject({ err: new Error('图片加载失败'), data: null });
        };
        img.src = src;
    })
}

// 监听音频加载完成
function loadAudio(src) {
    return new Promise((resolve, reject) => {
        const audio = new Audio();
        audio.onloadedmetadata = function () {
            resolve({ err: null, data: audio })
        };
        audio.onerror = function () {
            reject({ err: new Error('音频加载失败'), data: null })
        };
        audio.src = src;
    })
}

export default {
    name: "HomeComponent",
    data() {
        return {
            bgWav,// '@/assets/wav/boot.wav'
            eatFoodWav,// '@/assets/wav/apple.wav'
            eatBonusWav,// '@/assets/wav/bonus.wav'
            hitWallWav,// '@/assets/wav/hitWall.wav'
            GameOverWav,// '@/assets/wav/GameOver.wav'
            CongratsWav,// '@/assets/wav/Congrats.wav'
            upgradeWav,// '@/assets/wav/upgrade.wav'

            appleImg,// '@/assets/images/apple.png'
            bodyLeftImg,// '@/assets/images/body_left.png'
            bodyBottomImg,// '@/assets/images/body_bottom.png'
            bodyRightImg,// '@/assets/images/body_right.png'
            bodyTopImg,// '@/assets/images/body_top.png'
            footerImg,// '@/assets/images/footer.png'
            headerLeftImg,// '@/assets/images/header_left.png'
            headerBottomImg,// '@/assets/images/header_bottom.png'
            headerRightImg,// '@/assets/images/header_right.png'
            headerTopImg,// '@/assets/images/header_top.png'
            bFoodImg,// '@/assets/images/b.png'
            dieLeftImg,// '@/assets/images/die_left.png'
            dieRightImg,// '@/assets/images/die_right.png'
            dieTopImg,// '@/assets/images/die_top.png'
            dieBottomImg,// '@/assets/images/die_bottom.png'
            awardImg,// '@/assets/images/award.png'
            status: -1,
            digitImgList: [
                digitImg0,
                digitImg1,
                digitImg2,
                digitImg3,
                digitImg4,
                digitImg5,
                digitImg6,
                digitImg7,
                digitImg8,
                digitImg9,
            ],
            levelImg: levelImg,
            //分数
            grader: 0,
            appleNum: 0,
            //蛇运动的速度
            speed: 300,
            bNum: 0,
            isSucess: false,
            isStarted: false
        };
    },
    computed: {
        graderShow() {
            return String(this.grader).split("");
        },
        level() {
            return Math.floor(this.appleNum / 8) + 1
        }
    },
    watch: {
        level() {
            this.isSucess = true
            snake && snake.stop()
            this.$nextTick(() => {
                AUDIO.UPGRADE.play()
                setTimeout(() => {
                    this.isSucess = false
                    snake && snake.start()
                }, 1000)
            })
        }
    },
    methods: {
        game() {
            const app = this;
            var canvas = document.getElementById("canvas");
            var context = canvas.getContext("2d");

            function getImg(imgs, direction) {
                let img
                //设置蛇头的运动方向，37 左，38 上，39 右，40 下
                if (direction === 'left') {
                    img = imgs.left
                } else if (direction === 'right') {
                    img = imgs.right
                } else if (direction === 'top') {
                    img = imgs.top
                } else {
                    img = imgs.bottom
                }
                return img
            }

            //构造对象方块
            function Img(x, y, img, direction, name) {
                this.x = x;
                this.y = y;
                this.direction = direction;
                this.img = img;
                this.name = name || ''
            }

            Img.prototype.getImg = function () {
                let img = getImg(this.img, this.direction)
                return img
            }
            //画方块的方法
            Img.prototype.draw = function () {
                let img = this.getImg()
                if (this.name === 'header') {
                    //设置蛇头的运动方向，37 左，38 上，39 右，40 下
                    if (snake.head.direction === 'top') {
                        context.drawImage(img, this.x + 8, this.y - 8, img.width, img.height)
                    }
                    else if (snake.head.direction === 'bottom') {
                        context.drawImage(img, this.x + 4, this.y, img.width, img.height)
                    }
                    else if (snake.head.direction === 'right') {
                        context.drawImage(img, this.x + 8, this.y - 4, img.width, img.height)
                    }
                    else {
                        context.drawImage(img, this.x, this.y - 4, img.width, img.height)
                    }

                } else {
                    context.drawImage(img, this.x, this.y, img.width, img.height)
                }
            };

            //构造对象蛇
            function Snake() {
                this.init()
            }
            Snake.prototype.start = function () {
                if (!app.isStarted) {
                    AUDIO.BG.play()
                    app.isStarted = true
                }
                food.draw();
                snake.draw();
                foodB && foodB.draw();
                timer = setInterval(function () {
                    context.clearRect(0, 0, canvas.width, canvas.height);
                    food.draw();
                    foodB && foodB.draw();
                    snake.move();
                    snake.draw();
                    award && award.draw()
                }, app.speed);
            }

            Snake.prototype.stop = function () {
                AUDIO.BG.pause()
                clearInterval(timer);
                timer = null;
            }
            Snake.prototype.init = function () {
                context.clearRect(0, 0, canvas.width, canvas.height);
                //定义一个空数组存放组成整蛇的方块对象
                var snakeArray = [];
                const startX = 104
                const startY = 124
                //画出4个方块，设置成灰色
                for (var i = 0; i < 8; i++) {
                    var rect
                    if (i === 7) {
                        rect = new Img((i - 1) * 12 + 20 + startX, startY, {
                            left: IMGS.FOOTER,
                            right: IMGS.FOOTER,
                            top: IMGS.FOOTER,
                            bottom: IMGS.FOOTER
                        }, 'left');
                    } else if (i === 0) {
                        rect = new Img(startX, startY, {
                            left: IMGS.HEADERLEFT,
                            right: IMGS.HEADERRIGHT,
                            top: IMGS.HEADERTOP,
                            bottom: IMGS.HEAERBOTTOM
                        }, 'left', 'header');
                    } else {
                        if (
                            i === 1 || i === 3 || i === 5
                        ) {
                            rect = new Img((i - 1) * 12 + 20 + startX, startY, {
                                left: IMGS.FOOTER,
                                right: IMGS.FOOTER,
                                top: IMGS.FOOTER,
                                bottom: IMGS.FOOTER
                            }, 'left')
                        } else {
                            rect = new Img((i - 1) * 12 + 20 + startX, startY, {
                                left: IMGS.BODYLEFT,
                                right: IMGS.BODYRIGHT,
                                top: IMGS.BODYTOP,
                                bottom: IMGS.BODYBOTTOM
                            }, 'left')
                        }
                    }
                    snakeArray.push(rect);
                }

                //此处将两个后面常用的东西定为属性，方便后面调用
                this.head = snakeArray[0]; //蛇头
                this.snakeArray = snakeArray; //整蛇数组

                //给定初始位置向左(同keyCode右箭头)
                //设置蛇头的运动方向，37 左，38 上，39 右，40 下
                this.direction = 37;
            }
            Snake.prototype.checkHitWall = function () {
                //设置蛇头的运动方向，37 左，38 上，39 右，40 下
                let x
                let y
                let img
                if (this.head.direction === 'left') {
                    x = this.head.x
                    y = this.head.y - 4
                    img = this.head.img.left
                } else if (this.head.direction === 'right') {
                    x = this.head.x + 8
                    y = this.head.y - 4
                    img = this.head.img.right
                } else if (this.head.direction === 'top') {
                    x = this.head.x + 8
                    y = this.head.y - 8
                    img = this.head.img.top
                } else {
                    x = this.head.x + 4
                    y = this.head.y
                    img = this.head.img.bottom
                }
                return !(
                    x > 0 &&
                    y > 0 &&
                    x + img.width < canvas.width &&
                    y + img.height < canvas.height
                )
            }
            Snake.prototype.checkHitSelf = function (selfs) {
                const head = getImg(snake.head.img, snake.head.direction)
                const self = getImg(selfs.img, selfs.direction)
                let bool

                if (this.head.direction === 'left') {
                    let x = snake.head.x
                    let y = snake.head.y - 4
                    bool = isCollision(
                        x, y + 4, head.width, head.height - 4,
                        selfs.x, selfs.y, self.width, self.height,
                    )
                } else if (this.head.direction === 'right') {
                    let x = snake.head.x + 8
                    let y = snake.head.y - 4
                    bool = isCollision(
                        x, y + 4, head.width, head.height - 4,
                        selfs.x, selfs.y, self.width, self.height,
                    )
                } else if (this.head.direction === 'top') {
                    let x = snake.head.x + 8
                    let y = snake.head.y - 8
                    bool = isCollision(
                        x, y, head.width - 4, head.height,
                        selfs.x, selfs.y, self.width, self.height,
                    )
                } else {
                    let x = snake.head.x + 4
                    let y = snake.head.y
                    bool = isCollision(
                        x + 4, y, head.width - 4, head.height,
                        selfs.x, selfs.y, self.width, self.height,
                    )
                }
                return bool
            }
            //画蛇的方法
            Snake.prototype.draw = function () {
                for (var i = this.snakeArray.length - 1; i >= 0; i--) {
                    this.snakeArray[i].draw();
                }
            };
            //蛇移动的方法
            Snake.prototype.move = function () {
                //此处是核心部分，蛇的 移动方式
                //1、画一个灰色的方块，位置与蛇头重叠
                //2、将这个方块插到数组中蛇头后面一个的位置
                //3、砍去末尾的方块
                //4、将蛇头向设定方向移动一格


                if (app.appleNum != 0 && app.appleNum % 6 === 0 && app.bNum != app.appleNum / 6) {
                    if (foodB === null) {
                        foodB = new getRandomFood(IMGS.BFOOD);
                    }
                }

                let tmp = JSON.parse(JSON.stringify(this.head))
                //设置蛇头的运动方向，37 左，38 上，39 右，40 下
                switch (this.direction) {
                    case 37:
                        this.head.x -= 12;
                        this.head.direction = 'left';
                        break;
                    case 38:
                        this.head.y -= 12;
                        this.head.direction = 'top';
                        break;
                    case 39:
                        this.head.x += 12;
                        this.head.direction = 'right';
                        break;
                    case 40:
                        this.head.y += 12;
                        this.head.direction = 'bottom';
                        break;
                    default:
                        break;
                }

                for (let i = 1; i < this.snakeArray.length; i++) {
                    let x = tmp.x
                    let y = tmp.y
                    let direction = tmp.direction
                    tmp = JSON.parse(JSON.stringify(this.snakeArray[i]))
                    if (i === 1) {
                        this.snakeArray[i].x = x + 8
                    } else {
                        this.snakeArray[i].x = x
                    }
                    this.snakeArray[i].y = y
                    this.snakeArray[i].direction = direction

                }


                if (foodB && isEat(foodB)) {
                    let x = foodB.x
                    let y = foodB.y
                    const img = IMGS.AWARD
                    if (x + img.width > canvas.width) {
                        x = canvas.width - img.width
                    }
                    if (y + img.height > canvas.height) {
                        y = canvas.height - img.height
                    }
                    foodB = null
                    app.grader += 1000
                    app.bNum += 1
                    AUDIO.EATBONUS.play();
                    award = new Img(x, y, {
                        left: img,
                        right: img,
                        top: img,
                        bottom: img
                    }, 'left')
                    setTimeout(() => {
                        award = null
                    }, 500)
                }

                //判断是否吃到食物，isEat判定函数写在最后了
                //吃到则食物重新给位置，不砍去最后一节，即蛇变长
                //没吃到则末尾砍掉一节，即蛇长度不变
                let flag = isEat(food)
                if (flag) {
                    AUDIO.EATFOOD.play();
                    app.grader += 500
                    app.appleNum += 1
                    food = new getRandomFood(IMGS.APPLE);

                }

                if (flag) {
                    let last = this.snakeArray[this.snakeArray.length - 1]
                    this.snakeArray.splice(this.snakeArray.length - 1, 0, new Img(last.x, last.y, {
                        left: IMGS.FOOTER,
                        right: IMGS.FOOTER,
                        top: IMGS.FOOTER,
                        bottom: IMGS.FOOTER
                    }, 'left'));
                    last = this.snakeArray[this.snakeArray.length - 1]
                    this.snakeArray.splice(this.snakeArray.length - 1, 0, new Img(last.x, last.y, {
                        left: IMGS.BODYLEFT,
                        right: IMGS.BODYRIGHT,
                        top: IMGS.BODYTOP,
                        bottom: IMGS.BODYBOTTOM
                    }, 'left'));
                }


                // gameover判定
                // 撞墙
                if (this.checkHitWall()) {
                    let die
                    if (snake.head.direction === 'left') {
                        let img = IMGS.DIELEFT
                        let x = snake.head.x
                        let y = snake.head.y - 4 + snake.head.img.left.height / 2 - img.height / 2
                        die = new Img(x, y, { left: img }, 'left')
                    }
                    else if (snake.head.direction === 'right') {
                        let img = IMGS.DIERIGHT
                        let y = snake.head.y - 4 + snake.head.img.right.height / 2 - img.height / 2
                        let x = canvas.width - img.width
                        die = new Img(x, y, { right: img }, 'right')
                    }
                    else if (snake.head.direction === 'top') {
                        let img = IMGS.DIETOP
                        let y = snake.head.y - 8
                        let x = snake.head.x + 8 + snake.head.img.top.width / 2 - img.width / 2
                        die = new Img(x, y, { top: img }, 'top')
                    }
                    else {
                        let img = IMGS.DIEBOTTOM
                        let y = canvas.height - img.height
                        let x = snake.head.x + 5 + snake.head.img.bottom.width / 2 - img.width / 2
                        die = new Img(x, y, { bottom: img }, 'bottom')
                    }
                    die.draw()

                    AUDIO.DIE.play();
                    AUDIO.BG.pause()
                    clearInterval(timer);
                    timer = null
                    setTimeout(() => {
                        app.status = 3
                        app.$nextTick(() => {
                            AUDIO.GAMEOVER.play()
                        })
                    }, 1000)
                }

                // 撞自己，循环从1开始，避开蛇头与蛇头比较的情况
                for (let i = 3; i < this.snakeArray.length; i++) {
                    if (
                        this.checkHitSelf(this.snakeArray[i])
                    ) {
                        AUDIO.DIE.play();
                        AUDIO.BG.pause()
                        clearInterval(timer);
                        timer = null
                        setTimeout(() => {
                            app.status = 3
                        }, 1000)
                    }
                }
            };


            //画出初始的蛇
            snake = new Snake();
            //画出初始的食物
            food = new getRandomFood(IMGS.APPLE);

            //随机函数，获得[min,max]范围的值
            function getNumberInRange(min, max) {
                var range = max - min;
                var r = Math.random();
                return Math.round(r * range + min);
            }

            function getRandomFood(img) {
                //判定食物是否出现在蛇身上，如果是重合，则重新生成一遍
                var isOnSnake = true;

                //设置食物出现的随机位置
                while (isOnSnake) {
                    //执行后先将判定条件设置为false，如果判定不重合，则不会再执行下列语句
                    isOnSnake = false;
                    var indexX = getNumberInRange(2, Math.floor(canvas.width / img.width) - 2);
                    var indexY = getNumberInRange(2, Math.floor(canvas.height / img.height) - 2);
                    var rect = new Img(indexX * img.width + 4, indexY * img.height + 4, {
                        left: img,
                        right: img,
                        top: img,
                        bottom: img
                    }, 'left');
                    for (var i = 0; i < snake.snakeArray.length; i++) {
                        let self = snake.snakeArray[i]
                        if (i === 0) {
                            let bool = isEat(rect)
                            if (bool) {
                                //如果判定重合，将其设置为true，使随机数重给
                                isOnSnake = true;
                                break;
                            }
                        } else {
                            let bool = isCollision(
                                self.x, self.y, self.img.left.width, self.img.left.height,
                                rect.x, rect.y, rect.img.left.width, rect.img.left.height,
                            )
                            if (bool) {
                                //如果判定重合，将其设置为true，使随机数重给
                                isOnSnake = true;
                                break;
                            }
                        }
                    }
                }
                //返回rect，使得实例化对象food有draw的方法
                return rect;
            }

            /**
             * @function 检查两个矩形是否碰撞
             * @param {Object} a 矩形a
             * @param {Object} b 矩形b
             * @returns {Boolean} bool true 表示碰撞了 false 表示没有碰撞
             */
            function isCollision(ax, ay, aw, ah, bx, by, bw, bh) {
                let bool = !(
                    bx >= ax + aw ||
                    by >= ay + bh ||
                    by <= ay - ah ||
                    bx <= ax - bw
                )
                return bool
            }

            //判定吃到食物，即蛇头坐标与食物坐标重合
            function isEat(foods) {
                const head = getImg(snake.head.img, snake.head.direction)
                const food = getImg(foods.img, foods.direction)
                let bool
                if (snake.head.direction === 'left') {
                    let x = snake.head.x
                    let y = snake.head.y - 4
                    bool = isCollision(
                        x, y, head.width, head.height,
                        foods.x, foods.y, food.width, food.height,
                    )
                } else if (snake.head.direction === 'right') {
                    let x = snake.head.x + 8
                    let y = snake.head.y
                    bool = isCollision(
                        x, y, head.width, head.height,
                        foods.x, foods.y, food.width, food.height,
                    )
                } else if (snake.head.direction === 'top') {
                    let x = snake.head.x + 8
                    let y = snake.head.y - 8
                    bool = isCollision(
                        x, y, head.width, head.height,
                        foods.x, foods.y, food.width, food.height,
                    )
                } else {
                    let x = snake.head.x + 4
                    let y = snake.head.y
                    bool = isCollision(
                        x, y, head.width, head.height,
                        foods.x, foods.y, food.width, food.height,
                    )
                }

                if (bool) {
                    return true;
                } else {
                    return false;
                }
            }

            //键盘事件，其中的if判定是为了让蛇不能直接掉头
            document.onkeydown = function (e) {
                var ev = e || window.event;
                if (app.status === 3) {
                    app.status = 1
                    app.isStarted = false
                    app.speed = 300
                    app.grader = 0
                    app.appleNum = 0
                    app.bNum = 0
                    snake.init()
                    return
                }
                //设置蛇头的运动方向，37 左，38 上，39 右，40 下
                switch (ev.keyCode) {
                    case 37: {
                        if (timer === null) {
                            return
                        }
                        if (isLock) {
                            return
                        }
                        isLock = true
                        if (snake.direction !== 39) {
                            snake.direction = 37;
                        }
                        setTimeout(() => {
                            isLock = false
                        }, app.speed)
                        break;
                    }
                    case 38: {
                        if (timer === null) {
                            return
                        }
                        if (isLock) {
                            return
                        }
                        isLock = true
                        if (snake.direction !== 40) {
                            snake.direction = 38;
                        }
                        setTimeout(() => {
                            isLock = false
                        }, app.speed)
                        break;
                    }
                    case 39: {
                        if (timer === null) {
                            return
                        }
                        if (isLock) {
                            return
                        }
                        isLock = true
                        if (snake.direction !== 37) {
                            snake.direction = 39;
                        }
                        setTimeout(() => {
                            isLock = false
                        }, app.speed)
                        break;
                    }
                    case 40: {
                        if (timer === null) {
                            return
                        }
                        if (isLock) {
                            return
                        }
                        isLock = true
                        if (snake.direction !== 38) {
                            snake.direction = 40;
                        }
                        setTimeout(() => {
                            isLock = false
                        }, app.speed)
                        break;
                    }
                    case 32: {
                        if (app.isSucess) {
                            return
                        }
                        if (app.status === 3) {
                            context.clearRect(0, 0, canvas.width, canvas.height);
                            return
                        }
                        if (app.status !== 2) {
                            app.status = 2
                            app.$nextTick(() => {
                                if (timer === null) {
                                    snake.start();
                                } else {
                                    snake.stop();
                                }
                            })
                        } else {
                            if (timer === null) {
                                snake.start();
                            } else {
                                snake.stop();
                            }

                        }

                        break;
                    }
                    // case 189: {
                    //     if (timer === null) {
                    //         return
                    //     }
                    //     if (app.isSucess) {
                    //         return
                    //     }
                    //     if(app.speed < 1000) {
                    //         app.speed += 50;
                    //     }
                    //     clearInterval(timer)
                    //     timer = null;
                    //     snake.start();
                    //     break;
                    // }
                    // case 187: {
                    //     if (timer === null) {
                    //         return
                    //     }
                    //     if (app.isSucess) {
                    //         return
                    //     }
                    //     if(app.speed > 50) {
                    //         app.speed -= 50;
                    //     }
                    //     clearInterval(timer)
                    //     timer = null;
                    //     snake.start();
                    //     break;
                    // }
                }
                // ev.preventDefault();
            };
        },
    },

    mounted() {
        const arr = [
            loadAudio(bgWav),
            loadAudio(eatFoodWav),
            loadAudio(eatBonusWav),
            loadAudio(hitWallWav),
            loadAudio(GameOverWav),
            loadAudio(CongratsWav),
            loadAudio(upgradeWav),
            loadImage(appleImg),
            loadImage(bodyLeftImg),
            loadImage(bodyBottomImg),
            loadImage(bodyRightImg),
            loadImage(bodyTopImg),
            loadImage(footerImg),
            loadImage(headerLeftImg),
            loadImage(headerBottomImg),
            loadImage(headerRightImg),
            loadImage(headerTopImg),
            loadImage(bFoodImg),
            loadImage(dieLeftImg),
            loadImage(dieRightImg),
            loadImage(dieTopImg),
            loadImage(dieBottomImg),
            loadImage(awardImg)
        ]
        Promise.all(arr).then(() => {
            AUDIO.BG = document.querySelector('#bg')
            AUDIO.EATFOOD = document.querySelector('#eatFood')
            AUDIO.EATBONUS = document.querySelector('#eatBonus')
            AUDIO.DIE = document.querySelector('#die')
            AUDIO.GAMEOVER = document.querySelector('#GameOver')
            AUDIO.UPGRADE = document.querySelector('#upgrade')

            IMGS.APPLE = document.querySelector('#apple')
            IMGS.BODYLEFT = document.querySelector('#bodyLeft')
            IMGS.BODYRIGHT = document.querySelector('#bodyRight')
            IMGS.BODYTOP = document.querySelector('#bodyTop')
            IMGS.BODYBOTTOM = document.querySelector('#bodyBottom')
            IMGS.FOOTER = document.querySelector('#footer')
            IMGS.HEADERLEFT = document.querySelector('#headerLeft')
            IMGS.HEADERRIGHT = document.querySelector('#headerRight')
            IMGS.HEADERTOP = document.querySelector('#headerTop')
            IMGS.HEAERBOTTOM = document.querySelector('#headerBottom')
            IMGS.BFOOD = document.querySelector('#bFood')
            IMGS.DIELEFT = document.querySelector('#dieLeft')
            IMGS.DIERIGHT = document.querySelector('#dieRight')
            IMGS.DIETOP = document.querySelector('#dieTop')
            IMGS.DIEBOTTOM = document.querySelector('#dieBottom')
            IMGS.AWARD = document.querySelector('#award')

            this.status = 1
            this.game();
        }).catch(() => {
        })

    },
};
</script>

